@import "@/assets/global.scss";






































.container {
  z-index: 99;
  width: 100%;
  height: 52px;
  position: fixed;
  top: 0;
  background: #FFFFFF;
  text-align: center;
}

  .banner {
    width: 100%;
    max-width: 414px;
    height: 100%;
    position: relative;
    margin: 0 auto;
    padding: 0 16px;
    box-sizing: border-box;

    > .link-img {
      position: absolute;
      left: 16px;
      top: 50%;
      transform: translate(0, -50%);
    }

    > .skip {
      position: absolute;
      right: 16px;
      top: 50%;
      transform: translate(0, -50%);
    }
  }
